import { View, Text } from 'react-native'
import React from 'react'
import { TouchableOpacity } from 'react-native'
import {useNavigation} from '@react-navigation/native';

export default function Info() {
    const navigation = useNavigation();
    return (
        <View style={{ flex: 1 }}>
            <View style={{ flex: 1, flexDirection: 'row' }}>
                <View style={{ flex: 1, alignItems: 'flex-start', justifyContent: "flex-end" }}>
                    <Text style={{ fontSize: 18, fontWeight: 'bold', marginLeft: 20 }}>Informasi Terbaru</Text>
                </View>
                <TouchableOpacity
                    onPress={() => navigation.navigate('Informasi')}
                    style={{ flex: 1, alignItems: 'flex-end', justifyContent: "flex-end" }}>
                    <Text style={{ fontSize: 12, color: '#1F5298', marginRight: 20 }}
                    >Lihat Semua</Text>
                </TouchableOpacity>
            </View>
            <View style={{ flex: 3, }}>

            </View>
        </View>
    )
}