import { NavigationContainer } from '@react-navigation/native';
import { createNativeStackNavigator } from '@react-navigation/native-stack';
import { createBottomTabNavigator } from '@react-navigation/bottom-tabs';
import Home from '../Home/home';
import Program from '../program/program';
import Admisi from '../admisi/admisi';
import Elearning from '../program/elearning';
import { StatusBar } from 'react-native';
import Informasi from '../informasi/infromasi'


const Stack = createNativeStackNavigator();
const Tab = createBottomTabNavigator();

function Navigasi() {
  return (
    <NavigationContainer>
      <Stack.Navigator>
        <Stack.Screen name="Elearnin" component={Tab1}
          options={{ headerShown: false }} />
        <Stack.Screen name='Elearning' component={Elearning} />
        <Stack.Screen name='Informasi' component={Informasi} 
        options={{ headerShown: false }} />
      </Stack.Navigator>
    </NavigationContainer>
  )
}
export default Navigasi;

export function Tab1() {
  return (
   <>
   <StatusBar backgroundColor={'white'} barStyle='dark-content'/>
    <Tab.Navigator screenOptions={{ headerShown: false, }}>
      <Tab.Screen name="Home" component={Home} />
      <Tab.Screen name="Program" component={Program} />
      <Tab.Screen name="Informasi" component={Informasi} />
      <Tab.Screen name="Admisi" component={Admisi} />
    </Tab.Navigator>
   </>
  )
}