import { View, Text } from 'react-native'
import React from 'react'
import { Image } from 'react-native'
import { ScrollView } from 'react-native'
import Menu from './menu'
import Info from './info'

export default function Home() {
  return (
    <ScrollView
      overScrollMode='never'
      showsVerticalScrollIndicator={false}
      style={{ flex: 1, backgroundColor: 'white' }}>

      {/*header */}
      <View style={{ height: 100, justifyContent: 'center' }}>
        <Text style={{ width: 200, marginLeft: 20, fontSize: 20, fontWeight: 'bold' }}>Halo, Mahasiswa
          Universitas Terbuka</Text>
      </View>

      {/*banner */}
      <View style={{ height: 140, marginTop:-15 }}>
        <Image
          style={{
            marginHorizontal: 20, marginVertical: 20, borderRadius: 20, backgroundColor: 'gray', height: 100,
            overlay: 'hidden'
          }}
        />
      </View>

      {/*menu */}
      <View style={{ height: 170, justifyContent:'center' }}>
        <Menu />
      </View>

      {/*informasi */}
      <View style={{ height: 214, marginTop:20}}>
        <Info/>
      </View>

      {/*pemberitahuan */}
      <View style={{ height: 246 }}>
        <View style={{ marginHorizontal: 20, marginVertical: 20, borderRadius: 20, backgroundColor: 'gray', height: 206 }}>

        </View>
      </View>

      {/*copyright */}
      <View style={{ height: 97, justifyContent: 'center', alignItems: 'center' }}>
        <Text style={{ fontSize: 14, fontWeight: 'bold' }}>copyright © 2023</Text>
      </View>
    </ScrollView>
  )
}