import { View, Text, FlatList } from 'react-native'
import React from 'react'
import { Image } from 'react-native'
import { TouchableOpacity } from 'react-native'

export default function Menu() {
    const data1 = [
        { nama: 'Kalender', icon: '', link: '', id: 1 },
        { nama: 'UPBJJ', icon: '', link: '', id: 2 },
        { nama: 'TBOK', icon: '', link: '', id: 3 },
        { nama: 'Hallo UT', icon: '', link: '', id: 4 },
    ]
    const data2 = [
        { nama: 'E-Campus', icon: '', link: '', id: 5 },
        { nama: 'UT Web', icon: '', link: '', id: 6 },
        { nama: 'Lacak BA', icon: '', link: '', id: 7 },
        { nama: 'UT Career', icon: '', link: '', id: 8 },
    ]
    return (
        <View style={{ flex: 1, alignItems: 'center', height: 170 }}>
            {/*data1 */}
            <View style={{ flexDirection: 'row', height: 90, width: '97%', justifyContent: 'center' }}>
                {data1.map((item) => (
                    <TouchableOpacity style={{ flex: 1 }}>
                        <Image key={item.id} style={{
                            height: 60, width: 60, borderRadius: 10, alignSelf: 'center',
                            justifyContent: 'center', alignItems: 'center'
                        }} />
                        <Text key={item.id} style={{ flex: 1, textAlign: 'center', marginTop: 5, fontSize: 12 }}>{item.nama}</Text>
                    </TouchableOpacity>
                ))}
            </View>
            <View style={{ flexDirection: 'row', height: 90, width: '97%', justifyContent: 'center', marginTop: 10 }}>
                {data2.map((itemm) => (
                    <TouchableOpacity style={{ flex: 1 }}>
                        <Image key={itemm.id} style={{
                            height: 60, width: 60,borderRadius: 10, alignSelf: 'center',
                            justifyContent: 'center', alignItems: 'center'
                        }} />
                        <Text key={itemm.id} style={{ flex: 1, textAlign: 'center', marginTop: 5, fontSize: 12 }}>{itemm.nama}</Text>
                    </TouchableOpacity>
                ))}
            </View>

        </View>
    )
}